@import "./variables.scss";
@import "./mixin.scss";

.site-list__item {
  @include font-bold(17px, 24px);
  display: block;
  height: 78px;
  padding: 0 30px;
  border-top: 1px solid #ececec;
}

.site-list__item a {
  position: relative;
  display: block;
  padding-left: 45px;
  padding-top: 25px;
  &:hover {
    opacity: 0.6;
  }
  &:active {
    opacity: 0.3;
  }
}

.site-list__link--search::before,
.site-list__link--basket::before {
  content: "";
  @include set-abs-size-tl(30px, 2px, 18px, 18px);
  background-image: url("/img/icon-search.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.site-list__link--basket::before {
  width: 21px;
  left: 0;
  background-image: url("/img/icon-cart.svg")
}


@media (min-width: $tablet-width) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .site-list__item {
    @include reset-padding();
    @include reset-border();
    @include font-bold(15px, 24px);
    position: relative;
    height: 50px;
    width: 167px;
    order: 3;
    z-index: 2;
  }

  .site-list__item:not(.site-list__item--basket):not(.site-list__item--search) {
    padding-top: 8px;
  }

  .site-list__item a {
    @include reset-padding();
  }

  .site-list__item--search {
    @include reset-border();
    position: absolute;
    top: 0;
    right: 315px;
    width: 82px;
    height: 89px;
    font-size: 0;
    border-right: 1px solid #ececec;
    border-bottom: 1px solid #ececec;
    box-sizing: border-box;
    order: 1;
  }

  .site-list__item--basket {
    @include font-bold(17px, 24px);
    @include reset-border();
    position: absolute;
    top: 0;
    right: 51px;
    width: 264px;
    height: 89px;
    padding-left: 51px;
    box-sizing: border-box;
    border-bottom: 1px solid #ececec;
    order: 2;
  }

  .site-list__item--search a {
    padding-top: 36px;
    padding-left: 33px;
  }

  .site-list__item--basket a {
    padding-top: 29px;
    padding-left: 31px;
  }

  .site-list__link--search::before {
    top: auto;
    left: auto;
  }

  .site-list__link--basket::before {
    top: 35px;
    left: -18px;
  }
}

@media (min-width: $desktop-width) {
  .site-list {
    width: 1150px;
    min-height: 89px;
  }

  .site-list__item {
    @include reset-border();
    min-height: 89px;
    width: 235px;
    box-sizing: border-box;
    order: unset;
  }

  .site-list__item:not(.site-list__item:nth-child(2)) {
    border-right: 1px solid #e7e7e7;
  }

  .site-list__item:not(.site-list__item--basket):not(.site-list__item--search) {
    padding-top: 30px;
    padding-left: 50px;
  }

  .site-list__item:nth-child(2) a {
    margin-left: -15px;
    margin-top: 1px;
  }

  .site-list__item:not(.site-list__item--search) {
    @include font-bold(17px, 24px);
  }

  .site-list__item--search {
    position: static;
    margin-left: auto;
    width: 84px;
    min-height: 89px;
    border: none;
    border-left: 1px solid #e7e7e7;
  }

  .site-list__item--basket {
    @include reset-padding();
    position: static;
    width: 317px;
    min-height: 89px;
    border: none;
    border-left: 1px solid #e7e7e7;
  }

  .site-list__item--basket a {
    @include reset-padding();
    padding-top: 31px;
    padding-left: 82px;
  }

  .site-list__link--basket::before {
    top: 35px;
    left: 31px;
  }
}
